<template>
  <div class="header">
    <div class="content-wrapper">
      <div class="avatar">
        <img width="64" height="64" :src="seller.avatar" alt="">
      </div>
      <div class="content">
        <div class="title">
          <span class="brand"></span>
          <span class="name">{{seller.name}}</span>
        </div>
        <div class="description">
          {{seller.description}}/{{seller.deliveryTime}}分钟送达
        </div>
        <div v-if="seller.supports" class="support">
          <span class="icon" :class="classMap[seller.supports[0].type]"></span>
          <span class="text">{{seller.supports[0].description}}</span>
        </div>
      </div>
      <div v-if="seller.supports" class="supper-cont" @click="showData">
        <span class="cont">{{seller.supports.length}}个</span>
        <i class="icon-thumb_up"></i>
      </div>
    </div>
    <div class="bulletin-wrapper" @click="showData">
      <span class="bulletin-title"></span><span class="bulletin-text">{{seller.bulletin}}</span>
      <i class="icon-thumb_up"></i>
    </div>
    <div class="background">
      <img class="background_img" :src="seller.avatar" alt="">
    </div>
    <transition name="move">
      <div v-show="detailSho" class="detail" transition="fade">
        <div class="detail-wrapper clearfix">
          <div class="detail-main">
            <h1 class="name">{{seller.name}}</h1>
            <div class="star-wrapper">
              <star :size="48" :score="seller.score"></star>
            </div>
            <div class="title">
              <div class="line"></div>
              <div class="text">优惠信息</div>
              <div class="line"></div>
            </div>
            <ul v-if="seller.supports" class="supports">
              <li class="supports-item" v-for="(item,index) in seller.supports">
                <span class="icon" :class="classMap[seller.supports[index].type]"></span>
                <span class="text">{{seller.supports[index].description}}</span>
              </li>
            </ul>
            <div class="title">
              <div class="line"></div>
              <div class="text">商家公告</div>
              <div class="line"></div>
            </div>
            <div class="bulletin">
              <p class="content">{{seller.bulletin}}</p>
            </div>
          </div>
        </div>
        <div class="detail-close">
          <i class="icon-remove_circle_outline" @click="hiddenData"></i>
        </div>
      </div>
    </transition>
  </div>
</template>

<script type="text/ecmascript-6">
  import star from '../start/start';

  export default {
    props: {
      seller: {
        type: Object
      }
    },
    data() {
      return {
        detailSho: false
      };
    },
    methods: {
      showData() {
        this.detailSho = true;
      },
      hiddenData() {
        this.detailSho = false;
      }
    },
    created() {
      this.classMap = ['decrease', 'discount', 'guarantee', 'invoice', 'special'];
    },
    components: {
      star
    }
  };
</script>

<style lang="stylus" rel="stylesheet/stylus">
  @import '../../common/stylus/maxin.styl'
  .header
    position: relative
    color: #ffffff
    background: rgba(7, 17, 27, 0.5)
    overflow: hidden
    .content-wrapper
      position: relative
      padding: 24px 12px 18px 24px
      font-size: 0
      .avatar
        vertical-align: top
        display: inline-block
        img
          border-radius: 2px
      .content
        display: inline-block
        margin-left: 16px
        font-size: 14px
        .title
          margin: 2px 0 8px 0
          .brand
            display: inline-block
            vertical-align: top
            width: 30px
            height: 18px
            bg-image: ('brand')
            background-size: 30px 18px  !important
            background-repeat: no-repeat !important
          .name
            margin-left: 6px
            font-size: 16px
            lint-height: 18px
            font-weight: bold
        .description
          font-size: 12px
          color: rgb(255, 255, 255)
          lint-height: 12px
          font-weight: 200
        .support
          margin: 10px 0 2px 0
          .icon
            display: inline-block
            width: 12px
            height: 12px
            background-size: 12px 12px !important
            background-repeat: no-repeat !important
            &.decrease
              bg-image: ('decrease_1')
            &.discount
              bg-image: ('discount_1')
            &.guarantee
              bg-image: ('guarantee_1')
            &.invoice
              bg-image: ('invoice_1')
            &.special
              bg-image: ('special_1')
          .text
            vertical-align: top
            font-size: 10px
            color: rgb(255, 255, 255)
            lint-height: 12px
            font-weight: 200
      .supper-cont
        position: absolute
        right: 12px
        right: 12px
        bottom: 18px
        padding: 0 8px
        height: 24px
        line-height: 24px
        border-radius: 14px
        background: rgba(0, 0, 0, 0.2)
        text-align: center
        .cont
          vertical-align: top
          font-size: 10px
        .icon-thumb_up
          font-size: 10px
          margin-left: 2px;
          line-height: 24px;
    .bulletin-wrapper
      position: relative
      height: 28px
      line-height: 28px
      padding: 0 22px 0 12px
      white-space: nowrap
      overflow: hidden
      text-overflow: ellipsis
      background: rgba(7, 17, 27, 0.2)
      .bulletin-title
        margin-top: 8px
        display: inline-block
        vertical-align: top
        width: 22px
        height: 12px
        bg-image('bulletin')
        background-size: 22px 12px !important
        background-repeat: no-repeat !important
      .bulletin-text
        font-size: 10px
        vertical-align: top
        font-weight: 200
        color: rgb(255, 255, 255)
        margin: 0 4px 0 4px
      .icon-thumb_up
        position: absolute
        right: 12px
        font-size: 10px
        top: 8px
    .background
      .background_img
        position: absolute
        left: 0
        top: 0
        width: 100%
        height: 100%
        z-index: -1;
        filter: blur(10px)
    .detail
      position: fixed
      z-index: 99
      top: 0
      left: 0
      width: 100%
      height: 100%
      overflow: auto
      opacity 1
      background: rgba(7, 17, 27, 0.8)
      &.move-enter-active, &.move-leave-active
        transition: all 0.5s
      &.move-enter, &.move-leave-active
        opacity: 0
        background: rgba(7, 17, 27, 0)
      .detail-wrapper
        min-height: 100%
        width: 100%
        .detail-main
          padding-top: 64px
          padding-bottom: 64px
          .name
            line-height: 16px
            text-align: center
            font-size: 16px
            font-family: 700
          .star-wrapper
            margin-top: 18px
            padding: 2px 0
            text-align: center
          .title
            display: flex
            width: 80%
            margin: 28px auto 24px auto
            .line
              flex: 1
              position: relative
              top: -6px
              border-bottom: 1px solid rgba(255, 255, 255, 0.2)
            .text
              padding: 0 12px
              font-weight: 700
              font-size: 14px
          .supports
            width: 80%
            margin: 0 auto
            .supports-item
              padding: 0 12px
              margin-bottom: 12px
              font-size: 0
              &.last-clild
                margin-bottom: 0
              .icon
                display: inline-block
                width: 16px
                height: 16px
                vertical-align: top
                margin-right: 6px
                background-size: 16px 16px !important
                background-repeat: no-repeat !important
                &.decrease
                  bg-image: ('decrease_2')
                &.discount
                  bg-image: ('discount_2')
                &.guarantee
                  bg-image: ('guarantee_2')
                &.invoice
                  bg-image: ('invoice_2')
                &.special
                  bg-image: ('special_2')
              .text
                line-height: 16px
                font-size: 12px
          .bulletin
            width: 80%
            margin: 0 auto
            font-size: 0
            .content
              padding: 0 12px
              font-weight: 200
              color: rgb(255, 255, 255)
              line-height: 24px
              font-size: 12px
      .detail-close
        position: relative
        width: 32px
        height: 32px
        margin: -64px auto 0 auto
        clear: both
        font-size: 32px
</style>



